'use client'

import React, { createContext, useContext, useState, useEffect } from 'react'
import { createBrowserClient } from './supabase/client'
import type { User } from './types'

interface AuthContextType {
  user: User | null
  isLoading: boolean
  login: (email: string, password: string) => Promise<void>
  signup: (email: string, password: string, firstName: string, lastName: string, role: 'student' | 'instructor', phone?: string) => Promise<void>
  logout: () => void
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const supabase = createBrowserClient()

  useEffect(() => {
    // Check for stored user on mount
    const storedUser = localStorage.getItem('lms_user')
    if (storedUser) {
      setUser(JSON.parse(storedUser))
    }
    setIsLoading(false)

    // Listen for auth changes
    const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
      if (session?.user) {
        const mappedUser: User = {
          id: session.user.id,
          email: session.user.email!,
          firstName: session.user.user_metadata.first_name || '',
          lastName: session.user.user_metadata.last_name || '',
          role: session.user.user_metadata.role || 'student',
          phone: session.user.user_metadata.phone,
          createdAt: session.user.created_at,
          updatedAt: new Date().toISOString()
        }
        setUser(mappedUser)
        localStorage.setItem('lms_user', JSON.stringify(mappedUser))
      } else {
        setUser(null)
        localStorage.removeItem('lms_user')
      }
    })

    return () => {
      subscription.unsubscribe()
    }
  }, [])

  const login = async (email: string, password: string) => {
    setIsLoading(true)
    try {
      const { data, error } = await supabase.auth.signInWithPassword({
        email,
        password
      })

      if (error) throw error

      if (data.user) {
        const mappedUser: User = {
          id: data.user.id,
          email: data.user.email!,
          firstName: data.user.user_metadata.first_name || '',
          lastName: data.user.user_metadata.last_name || '',
          role: data.user.user_metadata.role || 'student',
          phone: data.user.user_metadata.phone,
          createdAt: data.user.created_at,
          updatedAt: new Date().toISOString()
        }
        localStorage.setItem('lms_user', JSON.stringify(mappedUser))
        setUser(mappedUser)
      }
    } finally {
      setIsLoading(false)
    }
  }

  const signup = async (email: string, password: string, firstName: string, lastName: string, role: 'student' | 'instructor', phone?: string) => {
    setIsLoading(true)
    try {
      const { data, error } = await supabase.auth.signUp({
        email,
        password,
        options: {
          data: {
            first_name: firstName,
            last_name: lastName,
            role,
            phone
          }
        }
      })

      if (error) throw error

      if (data.user) {
        const mappedUser: User = {
          id: data.user.id,
          email: data.user.email!,
          firstName,
          lastName,
          role,
          phone,
          createdAt: data.user.created_at,
          updatedAt: new Date().toISOString()
        }
        localStorage.setItem('lms_user', JSON.stringify(mappedUser))
        setUser(mappedUser)
      }
    } finally {
      setIsLoading(false)
    }
  }

  const logout = async () => {
    await supabase.auth.signOut()
    localStorage.removeItem('lms_user')
    setUser(null)
  }

  return (
    <AuthContext.Provider value={{ user, isLoading, login, signup, logout }}>
      {children}
    </AuthContext.Provider>
  )
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider')
  }
  return context
}
